import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListofcountriesComponent } from './listofcountries/listofcountries.component';
import { CountryDetailsComponent } from './country-details/country-details.component';

const routes: Routes = [
{
  path:"",
  component:ListofcountriesComponent
},
{
  path:"countrydetails/:date",
  component:CountryDetailsComponent
}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const compimport=[ListofcountriesComponent,CountryDetailsComponent];
