import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CountrydetailService } from '../countrydetail.service';

@Component({
  selector: 'app-country-details',
  templateUrl: './country-details.component.html',
  styleUrls: ['./country-details.component.css']
})
export class CountryDetailsComponent implements OnInit {

  countrydetails:any={};
  ishidden:boolean=true;
  statewiseDetails:any[]=[];
  date:string="";
  selectState:string="";

  constructor(private route:ActivatedRoute,
              private service:CountrydetailService) { }

  ngOnInit() {
    this.getCountryDetails();

    if(this.countrydetails.country=="India"){
      this.ishidden=false;
    }else
    {
      this.ishidden=true;
    }
    this.getstatewisedetails();

  }

  getCountryDetails(){
    this.route.queryParamMap
    .subscribe(param=>{
      this.countrydetails={
        country:param.get('Country'),
        TotalConfirmed:param.get('TotalConfirmed'),
        TotalRecovered:param.get('TotalRecovered'),
        TotalDeaths:param.get('TotalDeaths'), 
        
      };     
     // console.log(this.countrydetails.country);

    });

    this.route.paramMap.subscribe(parammap=>{
      this.date=parammap.get('date');
      
    });
  }

  getstatewisedetails(){
    this.service.getstatewiseDetails()
    .subscribe(response=>{
      this.statewiseDetails=response['statewise'];
    });
  }

  onMouseOver(item){
    this.selectState=item.state;
  }
  MouseOut(){
    this.selectState="";
  }

}
