import { Component, OnInit } from '@angular/core';
import { ListcountryServiceService } from '../listcountry-service.service';

@Component({
  selector: 'app-listofcountries',
  templateUrl: './listofcountries.component.html',
  styleUrls: ['./listofcountries.component.css']
})
export class ListofcountriesComponent implements OnInit {

  listcountry:any[]=[];
  globaldetails={
    TotalConfirmed:"",
    TotalRecovered:"",
    TotalDeaths:""

  };
  date:string="";
  country:string="";
  indialive:any={};
  constructor(private service:ListcountryServiceService) { }

  ngOnInit() {
    this.getoflist();
  }

  public getoflist(){

    this.service.getListOfCountries()
    .subscribe((response)=>{
     // console.log(response);
    this.listcountry=response['Countries'];
   // console.log(this.listcountry);

  // this.globaldetails=response['Global'];
  // console.log("Total confirmed"+response['Global']['TotalConfirmed']);
      this.globaldetails={
        TotalConfirmed:response['Global']['TotalConfirmed'],
        TotalRecovered:response['Global']['TotalRecovered'],
        TotalDeaths:response['Global']['TotalDeaths']
      };
    //  console.log(this.globaldetails);
   this.date=response['Date'];
   this.date=this.date.slice(0,10);

   for (let index = 0; index < this.listcountry.length; index++) {
    if(this.listcountry[index].Country=="India"){
      this.indialive={
        Country:this.listcountry[index].Country,
        NewConfirmed:this.listcountry[index].NewConfirmed,
        NewRecovered:this.listcountry[index].NewRecovered,
        NewDeaths:this.listcountry[index].NewDeaths,
        TotalConfirmed:this.listcountry[index].TotalConfirmed,
        TotalRecovered:this.listcountry[index].TotalRecovered,
        TotalDeaths:this.listcountry[index].TotalDeaths,
        CountryCode:this.listcountry[index].CountryCode,
        Date:this.listcountry[index].Date,
        Slug:this.listcountry[index].Slug,

      };
    }
     
   }

    });

  }

  onMouseOver(item){
    this.country=item.Country;
  }
  MouseOut(){
    this.country="";
  }

}
